import React from 'react';
import PropTypes from 'prop-types';
import fetch from 'isomorphic-unfetch';
import ReactMarkdown from 'react-markdown';
import ScrollspyNav from 'react-scrollspy-nav';
import Divider from '@material-ui/core/Divider';
import Drawer from '@material-ui/core/Drawer';
import Hidden from '@material-ui/core/Hidden';
import IconButton from '@material-ui/core/IconButton';
import MenuIcon from '@material-ui/icons/Menu';
import grey from '@material-ui/core/colors/grey';
import green from '@material-ui/core/colors/green';
import red from '@material-ui/core/colors/red';
import { withStyles } from '@material-ui/core/styles';

const strapiUrl = 'http://localhost:1337';

const drawerWidth = 300;

const styles = theme => ({
  "@global": {
    body: {
      backgroundImage: 'url(/static/images/pattern.png)',
    },
  },
  root: {
    display: 'flex',
  },
  drawer: {
    [theme.breakpoints.up('md')]: {
      width: drawerWidth,
      flexShrink: 0,
    },
  },
  drawerPaper: {
    width: drawerWidth,
  },
  logo: {
    padding: 20,
  },
  content: {
    position: 'relative',
    flexGrow: 1,
  },
  menuButton: {
    position: 'absolute',
    left: 20,
    top: 20,
    zIndex: 20,
    color: '#fff',
    [theme.breakpoints.up('md')]: {
      display: 'none',
    },
  },
  photo: {
    position: 'relative',
    height: '100vh',
    backgroundRepeat: 'no-repeat',
    backgroundPosition: '85% top',
    backgroundSize: 'cover',
  },
  baseline: {
    position: 'absolute',
    left: 0,
    bottom: 0,
    width: '100%',
    height: '100%',
    padding: 20,
    backgroundColor: 'rgba(0,0,0,.3)',
    fontFamily: 'Roboto',
    fontSize: 28,
    color: '#fff',
    display: 'flex',
    flexDirection: 'column',
    justifyContent: 'flex-end',
    [theme.breakpoints.up('md')]: {
      justifyContent: 'flex-start',
      padding: 40,
    },
    [theme.breakpoints.down('xs')]: {
      fontSize: 22,
    },
  },
  markdown: {
    [theme.breakpoints.up('md')]: {
      width: '50%',
    },
  },
});

class En extends React.Component {
  state = {
    mobileOpen: false,
  };

  handleDrawerToggle = () => {
    this.setState(state => ({ mobileOpen: !state.mobileOpen }));
  };

  render() {
    const { classes, theme } = this.props;

    const drawer = (
      <div>
        <ScrollspyNav scrollTargetIds={['home', 'section-1', 'section-2', 'section-3']} activeNavClass="currentnav" scrollDuration="200">
          <ReactMarkdown className={classes.logo} source={this.props.hp['Logo / titre du site']} />
          <Divider />
          <a href="#section-1">section 1</a>
          <a href="#section-2">section 2</a>
          <a href="#section-3">section 3</a>
        </ScrollspyNav>
      </div>
    );

    return (
      <div className={classes.root}>
        <nav className={classes.drawer}>
          {/* The implementation can be swapped with js to avoid SEO duplication of links. */}
          <Hidden mdUp implementation="css">
            <Drawer
              container={this.props.container}
              variant="temporary"
              open={this.state.mobileOpen}
              onClose={this.handleDrawerToggle}
              classes={{
                paper: classes.drawerPaper,
              }}
            >
              {drawer}
            </Drawer>
          </Hidden>
          <Hidden smDown implementation="css">
            <Drawer
              classes={{
                paper: classes.drawerPaper,
              }}
              variant="permanent"
              open
            >
              {drawer}
            </Drawer>
          </Hidden>
        </nav>
        <main className={classes.content}>
          <IconButton
            color="inherit"
            aria-label="Open drawer"
            onClick={this.handleDrawerToggle}
            className={classes.menuButton}
          >
            <MenuIcon />
          </IconButton>
          <div id="home" className={classes.photo} style={{ backgroundImage: `url(${strapiUrl}${this.props.hp.Photo.url})` }}>
            <div className={classes.baseline}>
              <ReactMarkdown source={this.props.hp.Baseline} className={classes.markdown} />
            </div>
          </div>
          <div id="section-1">section 1 début <br /><br /><br /><br /><br /><br /><br /><br /><br /><br /><br /><br /><br /><br /><br /><br /><br /><br /><br /><br /><br /><br /><br /><br /><br /><br /><br /><br /><br /><br /> section 1 fin</div>
          <div id="section-2">section 2 début <br /><br /><br /><br /><br /><br /><br /><br /><br /><br /><br /><br /><br /><br /><br /><br /><br /><br /><br /><br /><br /><br /><br /><br /><br /><br /><br /><br /><br /><br /> section 2 fin</div>
          <div id="section-3">section 3 début <br /><br /><br /><br /><br /><br /><br /><br /><br /><br /><br /><br /><br /><br /><br /><br /><br /><br /><br /><br /><br /><br /><br /><br /><br /><br /><br /><br /><br /><br /> section 3 fin</div>
        </main>
        <style jsx global>{`
          .${classes.photo} blockquote {
            font-style: italic;
            text-align: justify;
            margin: 0;
          }
          .${classes.photo} blockquote p {
            margin: 0;
            line-height: 1.1em;
          }
          .${classes.photo} blockquote + p {
            text-align: right;
            margin: 10px 0 0;
          }
          .${classes.logo} p {
            margin: 0;
          }
          .${classes.logo} h1 {
            margin: 10px 0 0;
            font-size: 26px;
            color: ${green[500]};
          }
          .${classes.logo} h2,
          .${classes.logo} h3 {
            margin: 0;
          }
          .${classes.logo} h2 {
            font-size: 16px;
            color: ${grey[700]};
          }
          .${classes.logo} h3 {
            font-size: 12px;
            color: ${grey[700]};
          }
          .currentnav {
            font-weight: 700;
          }
          #section-3 {
            height: 100vh;
          }
        `}</style>
      </div>
    );
  }
}

En.propTypes = {
  classes: PropTypes.object.isRequired,
};

En.getInitialProps = async function () {
  const res = await fetch(`${strapiUrl}/homepages/5c823abbf844280cc880d81f`);
  const data = await res.json();

  return {
    hp: data
  }
};

export default withStyles(styles)(En);